.container {
  width: min(100% - 2rem, 70rem);
  margin-inline: auto;

  @media screen and (max-width: 1280px) {
    width: min(100% - 2rem, 43rem);
  }

  @media screen and (max-width: 740px) {
    width: min(100% - 2rem, 29rem);
  }

  @media screen and (max-width: 500px) {
    width: min(100% - 3rem, 29rem);
  }
}
